  
.expense-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.5rem;
    margin: 1rem 0;
    background-color: #202334;
    border-radius: 10px;
  }
  
  .expense-item__description {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: flex-end;
    flex-flow: column-reverse;
    justify-content: flex-start;
    flex: 1;
  }
  
  .expense-item h2 {
    color: #3a3a3a;
    font-size: 1rem;
    flex: 1;
    margin: 0 1rem;
    color: white;
  }
  
  .expense-item__price {
    font-size: 1rem;
    font-weight: bold;
    color: white;
    background-color: #E84855;
    border: 1px solid #E84855;
    padding: 0.5rem;
    border-radius: 12px;
  }
  
  .buttonEdit {
    font: inherit;
    font-weight: bold;
    background: #2D3047;
    border: none;
    padding: 0.25rem 1rem;
    cursor: pointer;
    font-size: 0.75rem;
    border-radius: 10px;
  }
  
  @media (min-width: 580px) {
    .expense-item__description {
      flex-direction: row;
      align-items: center;
      justify-content: flex-start;
      flex: 1;
    }
  
    .expense-item__description h2 {
      font-size: 1.25rem;
    }
  
    .expense-item__price {
      font-size: 1.25rem;
      padding: 0.5rem 1.5rem;
    }

    .button:hover,
    .button:active {
      background: #494b57;
    } 
  }